/* Root for Variable Declaration */
:root {
    --background: #166088;
    --foreground: #DBE9EE;
    --mainBackground: rgba(219,233,238, .8);
    --linkBorder: 3px solid;
}
html {
    box-sizing: border-box;
}
body {
    background-color: var(--background)
}
#headerBox {
    margin: 0 auto;
    width:70%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-evenly;
}
.header {
    flex: 1 0 50%;
}
#projectWindow {
    background-image: url(./img/background.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    width: 70%;
    height: 600px;
    margin: 2em auto;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 2px 2px black;
}
#textWindow {
    width: 80%;
    height: 80%;
    background: rgba(255,255,255, .8);
    padding: 2em;
    text-align: center;
    overflow-x: scroll;
    transition: opacity 0.5s ease-in-out;
}

#textWindow h1 {
    color: var(--background);
}

#profilePicture {
    width: 150px;
}

h1 {
    font-size: 250%;
    font-weight: 500;
}

h2 {
    font-size: 150%;
    font-weight: 500;
}

/* Header Styling */

/* Article Styling - Each section is a separate article but styled the same*/
article {
    display: flex;
    width: 100%;
    margin: 25px auto;
    padding: 25px 0;
    justify-content: flex-start;
    flex-direction: column;
    overflow: hidden;
    overflow-y: scroll;
    align-items: center;
    opacity: 0;
    transition: all 1s;
    color: darkslategray)
}
    article .headline {
        width: 75%;
        text-align: center;
        margin: 0 auto;
        color: darkslategray;
        
    }
    article .description {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width:85%;
    }
    article .visible {
        opacity: 1;
    }
#about {
    display: flex;
}
