.cardDescription {
    max-width: 80%;
}
.cardDescription p {
    width: 95%;
    overflow-x: hidden;
    text-overflow: ellipsis;
}
/* Contact Section Styling */
#contactList ul {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    text-align: center;
    justify-content: space-evenly;
    padding-left: 0;

}

    #contactList ul li {
        display: inline-block;
        padding:30px 10px 5px 10px;
    }
        #contactList ul li a {
            text-decoration: none;
            transition: color .7s;
            color: var(--background);
        }
        #contactList ul li:hover a {
            color: darkslategray;
            border-bottom: var(--linkBorder) darkslategray;
        }

/* Resume Styling */
.resumeSection a {
    margin: 0 auto;
    text-decoration: none;
    padding:30px 10px 5px 10px;
    transition: color .7s;
    color: var(--background);
}
.resumeSection a:hover {
    color: darkslategray;
    border-bottom: var(--linkBorder) darkslategray
}
    .resumeSection img {
        height:30px;
    }
/* Footer Styling */
footer{
    display: flex;
    background-color: var(--background);
    color: var(--foreground);
    height: 50px;
    margin: 25px 0;
    align-items: center;
    justify-content: center;
}
footer i {
    font-size: 2rem;
    padding: 0 10px;
    color: var(--foreground);
}

.accordion-body {
    background-color:var(--mainBackground);
}

/* Media Queries for smaller screens */
@media screen and (max-width: 950px) {
    #jumbotron {
        display: none;
    }
    header {
        height: 120px;
    }
    header div {
        flex: 1 1 350px;
    }
    header div h3 {
        display: block;
    }
    header h1,
    header nav {
      margin: 0 auto;
    }
    header nav {
        display: none;
    }
    article {
        width: 100%;
        flex-wrap: wrap;
        justify-content: center;
    }
    article .headline {
        text-align: center;
    }
    article .description {
        width: 90%;
        border: 0;
    }
    #accordionSection {
        display:block
    }
    #nonAccordionSection {
        display: none;
    }
    #contactList ul {
        padding: 0;
    }
    .resumeSection {
        text-align: center;
    }

}
@media screen and (max-width: 674px) {
    header div {
        width: 100%;
        padding: 0;
        text-align: center;
    }
    header div h3 {
        display: block;
    }
    .bottomBorderLink {
        padding: 0;
    }
    #accordionSection {
        display:block
    }
    #nonAccordionSection {
        display: none;
    }
}
@media (hover:hover) {
    nav ul li:hover a{
        color: var(--background);
    }
}